<template>
  <div class="main-container">
    <div class="section-header">
      <img src="../src/assets/images/photo-png/logo.png" alt="Logo">
      <TimeComponent v-show="screenWidth > '1050'"/>
      <TimerComponent/>
      <div class="d-flex justify-space-around align-center">
        <v-btn
            id="menu-activator"
            color="primary"
            size="small"
            :disabled="isLoadedData === false"
        >
          <span v-if="screenWidth < '961'"
                style="display: flex;
                flex-direction: row;
                justify-content: center;
                align-items: center;
                gap: 3px"><v-icon>mdi-menu</v-icon>Совещание</span>
          <span v-if="screenWidth > '960'">Выбор совещания</span>
        </v-btn>
        <v-menu activator="#menu-activator">
          <v-list
              style="margin-top: 5px;"
          >
            <v-list-item
                v-for="(item, index) in menuItems"
                :key="index"
                :value="index"
                @click="chooseCurrentMeeting(item.name)"
                :style="{color: item.color}"
            >
              <v-list-item-title>
                <v-icon :icon="item.icon" style="margin: 0 3px 0 0" :style="{color: item.color}"></v-icon>
                {{ item.title }}
              </v-list-item-title>
            </v-list-item>
          </v-list>
        </v-menu>
      </div>
    </div>
    <div class="section-body">
      <v-progress-circular
          :size="70"
          width="7"
          color="blue-darken-2"
          indeterminate
          v-if="isLoadedData === false"
      ></v-progress-circular>
      <MeetingComponent v-if="isLoadedData === true" :meet="currentMeeting"/>
    </div>
    <div class="section-asside">
      <div class="section-asside__title">MEETINGS & CONFERENCES</div>
      <div class="section-asside__logo">
        <img src="./assets/images/photo-png/logo.png" alt="">
      </div>
    </div>
    <div class="section-footer">
      © TNP Meetings 2025 - {{ footerYear }}
    </div>
  </div>
</template>

<script>
import TimeComponent from "@/components/header/TimeComponent.vue";
import TimerComponent from "@/components/others/TimerComponent.vue";
import MeetingComponent from "@/components/MeetingComponent.vue";
import {dataStore} from "@/operations/data_store";
import $ from "jquery";
import Swal from "sweetalert2";

export default {
  name: 'App',
  components: {TimeComponent, TimerComponent, MeetingComponent},
  data(){
    return {
      footerYear: '',
      screenWidth: window.innerWidth,
      isLoadedData: false,
      menuItems: [
        {title: 'РМ', icon: 'mdi-account-multiple', color: '', name: 'rm'},
        {title: 'Продажи', icon: 'mdi-account-multiple', color: '', name: 'sales'},
        {title: 'Металл', icon: 'mdi-account-multiple', color: '', name: 'steel'},
        {title: 'Освещение', icon: 'mdi-account-multiple', color: '', name: 'light'},
        {title: 'Фарфор', icon: 'mdi-account-multiple', color: '', name: 'dish'},
        {title: 'Склад', icon: 'mdi-account-multiple', color: '', name: 'stock'},
        {title: 'ТОПы', icon: 'mdi-account-multiple', color: '', name: 'top'},
      ],
      currentMeeting: 'rm',
    }
  },
  methods: {
    footerData() {
      let date = new Date();
      this.footerYear = date.toLocaleDateString().slice(6);
    },
    updateWidth() {
      this.screenWidth = window.innerWidth;
    },
    async getMeetingData() {
      try {
        await $.ajax({
          url: 'https://script.google.com/macros/s/AKfycbwj7kz6tSVJ7STA8Mpg69iypuG0VzX4mQGFJg7t07sdX0ap1Kl2O_CBRhmVVfLWlnSFAw/exec',
          type: "GET",
          crossDomain: true,
          dataType: "json",
          success: (response) => {
            dataStore.setMeetingData(response);
            this.isLoadedData = true;

            const Toast = Swal.mixin({
              toast: true,
              position: "top-end", // базовое положение
              showConfirmButton: false,
              timer: 2000,
              timerProgressBar: true,
              didOpen: (toast) => {
                toast.onmouseenter = Swal.stopTimer;
                toast.onmouseleave = Swal.resumeTimer;

                // кастомное позиционирование
                toast.style.top = "70px";   // отступ сверху
              }
            });

            Toast.fire({
              icon: "success",
              title: "Данные загружены",
              background: "#56ae56",
              color: "white",
            });
          },
          error: (error) => {
            Swal.fire({
              title: "Ошибка!",
              text: 'Страница будет перезагружена',
              icon: "error",
              timer: 3000,
              timerProgressBar: true,
              showConfirmButton: false
            }).then(() => {
              console.error("Ошибка при загрузке:", error);
              window.location.reload();
            });
          }
        });
      } catch (err) {
        console.error("Непредвиденная ошибка:", err);
      }
    },
    chooseCurrentMeeting(meet){
      this.currentMeeting = meet;
    }
  },
  created() {
    window.addEventListener('resize', this.updateWidth);
    this.getMeetingData();
  },
  mounted() {
    this.footerData();
  },
  beforeUnmount() {
    window.removeEventListener('resize', this.updateWidth);
  }
}
</script>

