<template>
  <div class="time-container">
    <div class="date">{{ dateString }}</div>
    <div class="date-time-separator"></div>
    <div id="clock-container">
      <span id="hours">{{ hours }}</span>:
      <span id="minutes">{{ minutes }}</span>:
      <span id="seconds">{{ seconds }}</span>
    </div>
  </div>
</template>

<script>
export default {
  name: "TimeComponent",
  data() {
    return {
      seconds: '',
      minutes: '',
      hours: '',
      dateString: ''
    };
  },
  methods: {
    updateTime() {
      const now = new Date();
      this.seconds = (now.getSeconds() < 10 ? '0' : '') + now.getSeconds();
      this.minutes = (now.getMinutes() < 10 ? '0' : '') + now.getMinutes();
      this.hours = (now.getHours() < 10 ? '0' : '') + now.getHours();

      const days = [
        'Воскресенье',
        'Понедельник',
        'Вторник',
        'Среда',
        'Четверг',
        'Пятница',
        'Суббота'
      ];
      const day = now.getDay();
      const monthName = now.toLocaleString('default', {month: 'short'});
      const today = now.toLocaleString().slice(0, 2);

      this.dateString = `${today} ${monthName} ${days[day]}`;
    }
  },
  mounted() {
    this.updateTime();
    setInterval(this.updateTime, 1000);
  }
}
</script>

<style lang="scss" scoped>
.time-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  color: gray;

  #clock-container {
    display: flex;
  }

  .date-time-separator {
    width: 1px;
    background-color: gray;
    height: 20px;
    margin: 0 5px 0 7px;
  }

  .date {
    display: flex;
  }
}
</style>