<template>
  <div class="meeting-container" v-if="isLoadData">
    <div class="meeting-container-header">
      <img alt="photo" :src="getPhotoPath()">
    </div>

    <div class="meeting-container-title">
      <h3 v-if="currentMeetingData.meet_title" style="margin-bottom: 0">{{ currentMeetingData.meet_title }}</h3>
      <span class="meeting-date" v-if="currentMeetingData.meet_date">{{ currentMeetingData.meet_date }}</span>
      <h3 style="margin-top: 0" v-if="currentMeetingData.meet_time">Регламент: <span style="font-weight: bold; color: red">{{ currentMeetingData.meet_time }} мин.</span></h3>
      <p v-if="currentMeetingData.meet_format">
        <v-icon size="small" color="cyan">mdi-information-outline</v-icon>
        <span style="font-weight: bold; margin-left: 5px">Формат:</span>
        {{ currentMeetingData.meet_format }}
      </p>
      <p v-if="currentMeetingData.meet_members">
        <v-icon size="small" color="red">mdi-alert-outline</v-icon>
        <span style="font-weight: bold; margin-left: 5px">Участники:</span>
        {{ currentMeetingData.meet_members }}
      </p>
    </div>

    <div class="meeting-container-body-pre" v-if="currentMeetingData.meet_preambula">
      <v-alert
          border="start"
          border-color="red-accent-3"
          elevation="2"
          style="background-color: rgba(255,174,174,0.3)"
      >
        <h3>Преамбула<span style="color: red"></span></h3>
        <p>{{currentMeetingData.meet_preambula}}</p>
      </v-alert>
    </div>

    <div class="meeting-container-body">
      <v-alert
          v-for="item in currentMeetingData.items" :key="item.id"
          border="start"
          border-color="green-darken-1"
          elevation="2"
      >
        <div class="wrapper" style="display: flex; flex-direction: row; justify-content: space-between; align-items: center">
          <h3 v-if="item.title"> {{item.title}}</h3>
          <h3 v-if="(item.speaker_time + item.discussion_time) > 0" style="color: red"> {{item.speaker_time + item.discussion_time}} мин.</h3>
        </div>
        <p v-for="(subitem, index) in item.subitems" :key="index"><v-icon size="small" color="green-accent-3">mdi-check</v-icon>{{subitem}}</p>
        <v-divider style="margin: 5px"></v-divider>
        <p v-if="item.speaker"><v-icon size="small" color="orange">mdi-comment-account-outline</v-icon>Докладчики: {{ item.speaker }} <span v-if="item.speaker_time" style="color: red"> — {{ item.speaker_time }} мин.</span></p>
        <p v-if="item.discussion_time && item.discussion_time !== 0" ><v-icon size="small" color="blue">mdi-comment-alert-outline</v-icon>Обсуждение <span style="color: red"> — {{ item.discussion_time }} мин.</span></p>
      </v-alert>
    </div>
  </div>
</template>

<script>
import {dataStore} from "@/operations/data_store";

export default {
  name: 'MeetingComponent',
  props: {
    meet: {
      type: String,
      required: true
    }
  },
  data() {
    return {
      isLoadData: false,
      fullMeetingsData: null,
      currentMeeting: null,
      currentMeetingData: null,
    }
  },
  methods: {
    changeMeeting(meeting) {
      if (meeting === 'rm') {
        this.currentMeeting = 'rm';
        this.currentMeetingData = this.fullMeetingsData.meetings.rm;
      } else if (meeting === 'light') {
        this.currentMeeting = 'light';
        this.currentMeetingData = this.fullMeetingsData.meetings.light;
      } else if (meeting === 'sales') {
        this.currentMeeting = 'sales';
        this.currentMeetingData = this.fullMeetingsData.meetings.sales;
      } else if (meeting === 'stock') {
        this.currentMeeting = 'stock';
        this.currentMeetingData = this.fullMeetingsData.meetings.stock;
      } else if (meeting === 'dish') {
        this.currentMeeting = 'dish';
        this.currentMeetingData = this.fullMeetingsData.meetings.dish;
      } else if (meeting === 'steel') {
        this.currentMeeting = 'steel';
        this.currentMeetingData = this.fullMeetingsData.meetings.steel;
      } else if (meeting === 'top') {
        this.currentMeeting = 'top';
        this.currentMeetingData = this.fullMeetingsData.meetings.top;
      }
    },
    getPhotoPath() {
      try {
        return require(`@/assets/images/photo-png/${this.currentMeeting}.jpg`);
      } catch (error) {
        console.log(error);
      }
    },
  },
  mounted() {
    this.fullMeetingsData = dataStore.getMeetingData();
    this.currentMeeting = 'rm';
    this.currentMeetingData = this.fullMeetingsData.meetings.rm;
    this.isLoadData = true;
  },
  watch: {
    meet(newVal, oldVal) {
      if (newVal !== oldVal) {
        this.changeMeeting(newVal);
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.meeting-container {
  min-height: 100vh;
  z-index: 0;

  .meeting-container-header {
    width: 100%;
    height: 200px;
    overflow: hidden;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.5);

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center;
    }
  }

  .meeting-container-title {
    width: 100%;
    margin-bottom: 20px;
    position: relative;

    .meeting-date{
      position: absolute;
      top: 3px;
      right: 10px;
      color: cornflowerblue;
      font-weight: bold;
    }

    h3 {
      text-align: center;
      margin: 10px 0;
    }

    p {
      text-align: left;
      margin: 0 35px;
      color: grey;
      font-size: 17px;
    }
  }

  .meeting-container-body-pre {
    width: 95%;
    margin: 10px auto 15px auto;
    display: flex;
    flex-direction: column;
    gap: 15px;
    justify-content: center;
    align-items: center;

    .v-alert {
      text-align: left;
      width: 100%;

      h3 {
        margin-bottom: 10px;
      }

      p {
        margin-top: 5px;
        font-size: 17px;

        .v-icon {
          margin-right: 5px;
        }
      }
    }
  }

  .meeting-container-body {
    width: 95%;
    margin: 10px auto;
    display: flex;
    flex-direction: column;
    gap: 15px;
    justify-content: center;
    align-items: center;

    .v-alert {
      text-align: left;
      width: 100%;

      h3 {
        margin-bottom: 10px;
      }

      p {
        margin-top: 5px;
        font-size: 17px;

        .v-icon {
          margin-right: 5px;
        }
      }
    }
  }
}
</style>